import { Button, Form, Input } from "antd";
import React from "react";
import { useState } from "react";
import { useContext } from "react";
import { AiOutlineForm } from "react-icons/ai";
import { CrudContext } from "../../../context/CrudContext";
import { notificacion } from "../../../helpers/mensajes";
import MainModal from "../../modal/MainModal";

const ModalContrasenia = ({ modal, setModal }) => {
  const [form] = Form.useForm();

  const { dataToEdit, updateData, setDataToEdit } = useContext(CrudContext);
  const [password, setPassword] = useState({ nueva: "", nueva2: "" });
  const closeModal = () => {
    setModal(false);
    setDataToEdit(null);
  };

  const handleSubmit = async () => {
    if (password.nueva === password.nueva2) {
      const info = {
        contrasenia: password.nueva,
      };
      const route = "usuario/contrasenia";
      const response = await updateData(info, dataToEdit.id, route);
      if (response) {
        notificacion(response.status, response.msg);
        closeModal();
      }
    } else {
      notificacion(500, "Las contrase単as no coinciden.");
    }
  };

  const handleData = (e) => {
    const { name, value } = e.target;
    form.setFieldsValue({
      [name]: value,
    });
    setPassword((values) => {
      return { ...values, [name]: value };
    });
  };
  return (
    <MainModal
      className={"modal-usuario"}
      title={"Cambiar contrase単a"}
      open={modal}
      width={400}
      closeModal={closeModal}
    >
      <Form
        form={form}
        className="modal-body"
        onFinish={handleSubmit}
        layout="horizontal"
      >
        <Form.Item
          style={{ marginBottom: "8px" }}
          name="nueva"
          rules={[{ required: true, message: "Campo obligatorio!" }]}
        >
          <label htmlFor="">Nueva contrase単a</label>
          <Input.Password
            value={password.nueva}
            name="nueva"
            onChange={handleData}
          />
        </Form.Item>

        <Form.Item
          style={{ marginBottom: "8px" }}
          name="nueva2"
          rules={[{ required: true, message: "Campo obligatorio!" }]}
        >
          <label htmlFor="">Confirme nueva contrase単a</label>
          <Input.Password
            value={password.nueva2}
            name="nueva2"
            onChange={handleData}
          />
        </Form.Item>

        <Form.Item className="button-container">
          <Button
            htmlType="submit"
            icon={<AiOutlineForm />}
            // loading={cargando ? true : false}
          >
            Actualizar
          </Button>
        </Form.Item>
      </Form>
    </MainModal>
  );
};

export default ModalContrasenia;
